body {
  text-align: center;
  background-color: var(--rp-moon-base);
  color: var(--rp-moon-text);
  height: 100%;
}

footer {
  font-size: calc(2px + 1vmin);
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  text-align: left;
  background-color: var(--rp-moon-base);
}

img {
  width:100%;
  height: calc(100vh - 17rem);
  object-fit: fill;

}

.nav-link {
  color: var(--rp-moon-text);
  padding: .5% 1%;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}

.nav-link:hover {
  color: var(--rp-moon-rose);
}

#Name {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--rp-moon-text);
  border: 5px solid var(--rp-moon-iris);
  padding: 10px;
}

#Nav {
  border: 2px solid var(--rp-moon-iris);
}

#Navbar {
  display: inline-flex;
  column-count: auto;
  column-gap: 10px;
}

.page-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--rp-moon-text);
  border-bottom: 0.5px solid var(--rp-moon-iris);
}
